import Header from "./Header";
import { useHistory } from 'react-router-dom';
import './About.css';

function Home() {
  const history = useHistory();

  const birthdayclick = () => {
    console.log("birthdayclick  button is clicked ");
    history.push('/cakes');
  }

  const wineclick = () => {
    console.log("wineclick  button is clicked ");
    history.push('/cakes');
  }

  const aniversaryclick = () => {
    console.log("aniversaryclick button is clicked ");
    history.push('/cakes');
  }

  return (
    <div>
      <Header />

      <div id="carouselExampleIndicators" className="carousel slide" data-ride="carousel">
        <ol className="carousel-indicators">
          <li data-target="#carouselExampleIndicators" data-slide-to="0" className="active"></li>
          <li data-target="#carouselExampleIndicators" data-slide-to="1"></li>
          <li data-target="#carouselExampleIndicators" data-slide-to="2"></li>
        </ol>
        <div className="carousel-inner">
          <div className="carousel-item active" style={{ height: "500px" }}>
            <img className="d-block w-100" src="./images/carousal11.jpg" alt="First slide" />
          </div>
          <div className="carousel-item" style={{ height: "500px" }}>
            <img className="d-block w-100" src="./images/carousal12.jpg" alt="Second slide" />
          </div>
          <div className="carousel-item" style={{ height: "500px" }}>
            <img className="d-block w-100" src="./images/carousal13.jpg" alt="Third slide" />
          </div>
        </div>
        <a className="carousel-control-prev" href="#carouselExampleIndicators" role="button" data-slide="prev">
          <span className="carousel-control-prev-icon" aria-hidden="true"></span>
          <span className="sr-only">Previous</span>
        </a>
        <a className="carousel-control-next" href="#carouselExampleIndicators" role="button" data-slide="next">
          <span className="carousel-control-next-icon" aria-hidden="true"></span>
          <span className="sr-only">Next</span>
        </a>
      </div>

      <div className="container-fluid mt-3">

      </div>

      <div className="container">
        <div className="row">

          <div className="col-sm-4">
            <h3>Birthday Cakes</h3>
            <p>Celebrate every momemt using Cakes</p>
            <p>No birthday can be celebrated without a cake. </p>
            <p>We have a large collection of birthday cakes like black forest, truffle, chocolate, orange, red velvet, strawberry, pineapple, etc.</p>
            <p>So, pick up the favorite of your sweetheart and make this birthday a great one.</p>
            <button onClick={birthdayclick} className="btn btn-dark">Birthday Cakes</button><br />

          </div>

          <div className="col-sm-4">
            <h3>Aniversary cakes</h3>
            <p>Celebrate Special moments with our best cakes.</p>
            <p> Wedding is a sacred union of two individuals who are in love with each other.</p>
            <p>And thus, anniversary is the special day of celebration with the love of your life.</p>
            <p>  You must have planned a lovely surprise for your wife or husband surrounded by amazing gifts.</p>
            <button onClick={aniversaryclick} className="btn btn-dark">Aniversary cakes</button><br />
          </div>

          <div className="col-sm-4">
            <h3>Wine cakes</h3>
            <p> we work directly with the farmers to grow the fruit we work with. </p>
            <p> Our grapes are grown to exacting standards in some of the most diversely-layered vineyards around the world.</p>
            <p> The character of each Layer Cake wine is influenced by the vineyard soil,
              which is layered like a cake…every layer tells a story.</p>
            <button onClick={wineclick} className="btn btn-dark">Wine Cakes</button><br />

          </div>
        </div>
      </div>
    </div>

  );
}

export default Home;