import Header from "../Layout/Header";
import React, { useState } from 'react';

function AddProduct() {

    const [categoryid, setCategoryid] = useState("");
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [unitprice, setUnitprice] = useState("");
    const [quantityinstock, setQuantityinstock] = useState("");
    const [active, setActive] = useState("");
    const [imageurl, setImageurl] = useState("");
    const [likes, setLikes] = useState("");

    async function addProduct() {

        console.warn(categoryid, title, description, unitprice, quantityinstock, active, imageurl, likes);

        let item = { categoryid, title, description, unitprice, quantityinstock, active, imageurl, likes };

        console.warn(item);

        var result = await fetch('http://localhost:9796/api/cakes', {
            method: 'POST',
            headers: {
                "Content-Type": "application/json",
                "Accept": 'application/json'
            },
            body: JSON.stringify(item)
        });
        alert("Data has been saved" + result);
    }

    return (
        <div>
            <Header />
            <div className="col-sm-6 offset-sm-3">
                <h1 className="text-uppercase text-success">Add New Product</h1>
                <hr />
                <input type="text" placeholder="categoryid" onChange={(e) => setCategoryid(e.target.value)} className="form-control" /><br />
                <input type="text" placeholder="title" onChange={(e) => setTitle(e.target.value)} className="form-control" /><br />
                <input type="text" placeholder="description" onChange={(e) => setDescription(e.target.value)} className="form-control" /><br />
                <input type="text" placeholder="unitprice" onChange={(e) => setUnitprice(e.target.value)} className="form-control" /><br />
                <input type="text" placeholder="quantityinstock" onChange={(e) => setQuantityinstock(e.target.value)} className="form-control" /><br />
                <input type="text" placeholder="active" onChange={(e) => setActive(e.target.value)} className="form-control" /><br />
                <input type="text" placeholder="imageurl" onChange={(e) => setImageurl(e.target.value)} className="form-control" /><br />
                <input type="text" placeholder="likes" onChange={(e) => setLikes(e.target.value)} className="form-control" /><br />
                <button type="submit" onClick={addProduct} className="btn btn-success" >Add Product</button>

            </div>
        </div>
    )
}

export default AddProduct;