import React from "react";
import Header from "../Layout/Header";
import {Table} from 'react-bootstrap';
import OrderService from "./OrderService";

class Orders extends React.Component {

    handleClick(id) {
        OrderService.deleteOrder(id).then((res) =>{
            console.log(res);
        });      
    }

    constructor(props) {
        super(props);
        this.state = { orders: [] };
    }

    componentDidMount() {
        //fetch data from Rest API
        fetch('http://localhost:9796/api/orders')
            .then(
                async response => {
                    const data = await response.json();
                    if (!response.ok) {
                        const error = (data && data.message) || response.statusText;
                        return Promise.reject(error);
                    }
                    this.setState({ orders: data });
                    console.log(data);
                })
            .catch(error => {
                this.setState({ ErrorMessage: error.toString() });
                console.log("There was error while fetching data" + error);
            })
    }

    render() {

        return (

            <div >
            <Header/>
                <h3 className="text-uppercase text-success">Order Details </h3>
                <hr/>
                <div className="table-responsive" >
                {/* className="col-sm-8 offset-sm-2" */}
                    <Table striped bordered hover>
                        <thead>
                            <tr>
                                {/* <th>#</th> */}
                                <th>CustomerId</th>
                                <th>Name</th>
                                <th>Cake Ordered</th>
                                <th>Order Date</th>
                                <th>Order Amount</th>
                                <th>Status</th>
                                <th>Payment Mode</th>
                                <th>Delete Operation</th>
                            </tr>
                        </thead>
                        <tbody>
                            {
                                this.state.orders.map((item)=>                                
                                <tr>                         
                                <td>{item.customer_id}</td>
                                <td>{item.first_name} {item.last_name}</td>
                                <td>{item.title} </td>
                                <td>{item.order_date}</td>
                                <td>{item.order_amount}</td>
                                <td>{item.status}</td> 
                                <td>{item.payment_mode}</td>
                                <td><button onClick={() => this.handleClick(item.cake_id)} className="btn btn-danger">Delete</button></td>
                                </tr> 
                                )
                            }
                            
                            
                        </tbody>
                    </Table>
                </div>
                {/* {
                    orders.map(item => (
                        <Order order_id={item.order_id}
                            customer_id={item.customer_id}
                            cake_id={item.cake_id}
                            order_date={item.order_date}
                            order_amount={item.order_amount}
                            status={item.status}
                            payment_mode={item.payment_mode}
                        />
                    ))
                } */}
            </div>
        );
    }
}

export default Orders;