import React, {useState, useEffect} from 'react';
import { useHistory } from 'react-router-dom';
import Header from '../Layout/Header';


function Register(){

    useEffect(()=>{
        if(localStorage.getItem('user-info')){
            history.push('./cakes')
        }
        
    },[])

    const [firstname, setFirstName]=useState("");
    const [lastname, setLastName]=useState("");
    const [email, setEmail]=useState("");
    const [contactno, setContantNo]=useState("");
    const [password, setPassword]=useState("");
    const [cpassword, setCpassword]=useState("");
    const history=useHistory();

    async function submit(){
        
        let item={firstname, lastname, email, contactno, password, cpassword};

        console.warn(item);

        var result= await fetch("http://localhost:9796/api/customers", {
            method:'POST',
            headers:{
                "Content-Type":"application/json",
                "Accept": 'application/json'
            },
            body: JSON.stringify(item)
        });
        // result= await result;
        localStorage.setItem("user-info", JSON.stringify(result));
        history.push("/cakes");
    }
    

    return(
         <>
         <Header />
        <div className="col-sm-6 offset-sm-3">
            
            <h3 className="text-uppercase text-success"> New Customer Registration</h3>
            <hr/>
            <input type="text" placeholder="First Name" value={firstname} onChange={(e)=>setFirstName(e.target.value)} className="form-control"/>
            <br/>
            <input type="text" placeholder="Last Name" value={lastname} onChange={(e)=>setLastName(e.target.value)} className="form-control"/>
            <br/>
            <input type="email" placeholder="Email" value={email} onChange={(e)=>setEmail(e.target.value)} className="form-control"/>
            <br/>
            <input type="number" placeholder="Contant No" value={contactno} onChange={(e)=>setContantNo(e.target.value)} className="form-control"/>
            <br/>
            <input type="password" placeholder="Password" value={password} onChange={(e)=>setPassword(e.target.value)} className="form-control"/>
            <br/>
            <input type="password" placeholder="Confirm Password" value={cpassword} onChange={(e)=>setCpassword(e.target.value)} className="form-control"/>
            <br/>
            <button onClick={submit} className="btn btn-success">Submit</button>

        </div>
        </>
    )
}
export default Register;