import React from 'react';
import Header from '../Layout/Header';
import Counter from "./Counter";
import 'bootstrap/dist/css/bootstrap.min.css';


class RestList extends React.Component {

    constructor(props) {
        super(props);
        this.state = { products: [] };
        // this.state = { likes: props.likes };
        this.handler = this.handler.bind(this);
    }

    handler(data) {
        this.setState({ likes: data });
    }


    componentDidMount() {
        //fetch data from Rest API
        fetch('http://localhost:9796/api/cakes')
            .then(
                async response => {
                    const data = await response.json();
                    if (!response.ok) {
                        const error = (data && data.message) || response.statusText;
                        return Promise.reject(error);
                    }
                    this.setState({ products: data });
                    console.log(data);
                })
            .catch(error => {
                this.setState({ ErrorMessage: error.toString() });
                console.log("There was error while fetching data" + error);
            })
    }

    getStyle = () => {
        return {
            background: "#ffffff",
            padding: "10px",
            borderBottom: "1px #ccc dotted",
        };
    };

    render() {

        return (

            <div>
                <Header />
                <div style={this.getStyle()} >
                    <h3 className="text-uppercase text-success" >Bake N' Flake Best Cakes for Special Moments </h3>
                    <hr />
                    <div className="card-columns ">
                        {
                            this.state.products.map((item) =>

                                <div className="card bg-info text-white rounded" >

                                    <img className="card-img-top" src={item.imageurl} alt="Card image cap" />
                                    <div className="card-body">
                                        <h5 className="card-title">{item.title}</h5>
                                        <p className="card-text">{item.description} <br />
                                            Stock available: {item.quantity_in_stock}  <br />
                                            Price: {item.unit_price} Rs <br />
                                            Likes: {item.likes} <br />
                                            <Counter count={item.likes} handler={this.handler}></Counter></p>
                                    </div>
                                    <div class="card-footer">
                                        <button className="btn btn-dark">Add to Cart</button><br />
                                    </div>
                                </div>
                            )}
                    </div>
                </div>
            </div>
        );
    }
}

export default RestList;