import './Header.css';
import React from 'react';
import { Link, useHistory } from 'react-router-dom';
import { Navbar, Nav, NavDropdown } from 'react-bootstrap';
import { GiCakeSlice } from "react-icons/gi";

function Header() {

  const history = useHistory();

  const user = JSON.parse(localStorage.getItem('user-info'));
  console.warn(user);

  function logout() {
    localStorage.clear();
    history.push('/login');
  }

  return (

    <div >
      <Navbar bg="dark" variant="dark" >

        <Navbar.Brand href="/">Bake N' Flake  </Navbar.Brand>
        <Nav className="me-auto" >
          {
            localStorage.getItem('user-info') ?
              <>
                {/* <Link className="nav_bar_wrapper style={linkStyle} to="/update">Update Product</Link> */}
                <Link style={linkStyle} to="/cakes">Cakes</Link>
                <Link className="nav_bar_wrapper" style={linkStyle} to="/customers"> Customers</Link>
                <Link className="nav_bar_wrapper" style={linkStyle} to="/orders">Orders</Link>
                <Link className="nav_bar_wrapper" style={linkStyle} to="/addproduct">Add Product </Link>
                <Link className="nav_bar_wrapper" style={linkStyle} to="/about"><GiCakeSlice /></Link>
              </>
              :
              <>
                <Link className="nav_bar_wrapper" style={linkStyle} to="/login">Login</Link>
                <Link className="nav_bar_wrapper" style={linkStyle} to="/register">Register</Link>
                <Link className="nav_bar_wrapper" style={linkStyle} to="/about"><GiCakeSlice /></Link>
              </>
          }

        </Nav>
        {localStorage.getItem('user-info') ?
          <Nav>
            <NavDropdown title={user && user.first_name}>
              <NavDropdown.Item onClick={logout}>Logout</NavDropdown.Item>
              <NavDropdown.Item>Profile</NavDropdown.Item>
            </NavDropdown>
          </Nav>
          : null
        }
      </Navbar>
    </div >
  );
}

const linkStyle = {
  color: '#fff',
  textDecoration: 'none',
}

export default Header;